* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
}

.container {
  width: 100%;
}

.search-area {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5rem;
}

.search {
  position: relative;
  width: 60px;
  height: 60px;
  background: #fff;
  border-radius: 60px;
  transition: 0.5s;
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
}

.search .icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background: #fff;
  border-radius: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  cursor: pointer;
}

.search .icon::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  border: 3px solid #287dfc;
  border-radius: 50%;
  transform: translate(-4px, -4px) rotate(315deg);
}

.search .icon::after {
  content: "";
  position: absolute;
  width: 3px;
  height: 12px;
  background: #287dfc;
  transform: translate(6px, 6px) rotate(315deg);
}

.weather-grid {
  margin: 5rem 0;
  display: grid;
  padding: 10px;
  gap: 10px;
  width: 100%;
  grid-template-areas:
    "current"
    "D-one"
    "D-two"
    "D-three"
    "D-four"
    "D-five";
}

.currentDay {
  grid-area: current;
  display: grid;
  justify-content: center;
  gap: 10px;
  padding: 1.5rem;
  background: #fff;
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
}

.weather-article:nth-child(1) {
  grid-area: D-one;
}
.weather-article:nth-child(2) {
  grid-area: D-two;
}
.weather-article:nth-child(3) {
  grid-area: D-three;
}
.weather-article:nth-child(4) {
  grid-area: D-four;
}
.weather-article:nth-child(5) {
  grid-area: D-five;
}

.weather-article {
  display: grid;
  justify-content: center;
  padding: 1.5rem;
  gap: 1rem;
  grid-template-areas:
    "date"
    "temp"
    "wind"
    "humidity";
  background: #fff;
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
  border-radius: 15px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footer-content {
  display: grid;
  justify-content: center;
  padding: 1.5rem;
  gap: 1.5rem;
  grid-template-areas:
    "links"
    "copyright";
}

.links {
  grid-area: links;
  display: flex;
  justify-content: center;
}

.copyright {
  grid-area: copyright;
}

@media screen and (min-width: 35em) {
  .weather-grid {
    grid-template-areas:
      "current current"
      "D-one D-two"
      "D-three D-four"
      "D-five D-five";
  }
}

@media screen and (min-width: 60em) {
  .weather-grid {
    grid-template-areas:
      "current current current current current"
      "D-one D-two D-three D-four D-five";
  }
}
